<template>
    <div class="color-switch">
        <label class="switch">
            <input v-model="checked" type="checkbox" @click="switchScheme" />
            <span class="slider round"></span>
        </label>
    </div>
</template>

<script>
export default {
    name: 'ColorSwitch',
    data: function () {
        return {
            checked: false,
        }
    },
    mounted: function () {
        if (
            localStorage.colorScheme === 'light' ||
            document.documentElement.dataset.userColorScheme === 'light'
        ) {
            this.checked = false
            localStorage.colorScheme = 'light'
            document.documentElement.dataset.userColorScheme = 'light'
        } else {
            this.checked = true
            localStorage.colorScheme = 'dark'
            document.documentElement.dataset.userColorScheme = 'dark'
        }
    },
    methods: {
        switchScheme: function () {
            if (
                localStorage.colorScheme === 'light' ||
                document.documentElement.dataset.userColorScheme === 'light'
            ) {
                this.checked = true
                localStorage.colorScheme = 'dark'
                document.documentElement.dataset.userColorScheme = 'dark'
            } else {
                this.checked = false
                localStorage.colorScheme = 'light'
                document.documentElement.dataset.userColorScheme = 'light'
            }
        },
    },
}
</script>

<style lang="scss" scoped>
.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
    margin: 10px;
}

/* Hide default HTML checkbox */
.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

/* The slider */
.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: 0.4s;
    transition: 0.4s;
}

.slider:before {
    position: absolute;
    content: '';
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: 0.4s;
    transition: 0.4s;
}

input:checked + .slider {
    background-color: #2196f3;
}

input:focus + .slider {
    box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}
</style>
